@import url("https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap");

body {
  color: #fff;
  font-family: cursive;
  background-image: url(https://i.ibb.co/Swqkx75/Whats-App-Image-2021-11-12-at-5-05-56-PM.jpg);
  background-size: cover ;
  margin: 0;
  height: 100vh;
}

.App {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  background-color: rgba(99, 4, 99, 0.644);
  background: linear-gradient(
    180deg,
        rgba(139, 31, 139, 0.527) 0%,
        
    rgba(151, 79, 31, 0.418) 50%,
    rgba(150, 98, 29, 0.315) 100%
  );
}

.container {
  width: 100%;
  margin: 2% auto;
}
.col-4 {
  width: 25%;
  float: left;
}

h1 {
  font-size: 25px;
  text-transform: uppercase;
  line-height: 1;
}

@media (min-width: 768px) {
  .container {
    width: 1100px;
  }
  h1 {
    font-size: 35px;
  }
}
